import { Route, HashRouter as Router, Routes } from "react-router-dom";
import { createBrowserHistory } from "history";

import Home from "./pages/Home";
import MyAvatar from "./pages/MyAvatar";
import MyOwn3D from "./pages/MyOwn3D";
import Contact from "./pages/Contact";

function App() {
  const history = createBrowserHistory({ window });
  return (
    <div className="App">
      <Router>
        <Routes>
        <Route
          path={`/`}
          element={<Home history={history} />}
        />
        <Route
          path={`/pages/my-avatar`}
          element={<MyAvatar history={history} />}
        />
        <Route
          path={`/pages/own-3d`}
          element={<MyOwn3D history={history} />}
        />
        <Route
          path={`/pages/contact`}
          element={<Contact history={history} />}
        />
        <Route path="*" element={<NotFound history={history} />} />
        </Routes>
      </Router>
      
    </div>
  );
}

const NotFound = ({ history }: any) => {
  return <p className="mx-auto">Kosong</p>;
};

export default App;
